.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.page {
  width: 100%;
  max-width: 100%;
  height: 100%;
}
.page__body {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  min-height: 100%;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  background: linear-gradient(45deg, #a58f2b, #843f96);
  background-repeat: no-repeat;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.card {
  margin-top: 50%;
  width: 318px;
  height: 200px;
  background: linear-gradient(35deg, #fefefe 50%, #f0f0f0 -50%);
  border-radius: 10px;
  position: relative;
  box-shadow: #00000083 2px 30px 70px;
  border-top: 2px solid #b3b3b3;
  border-right: 2px solid #b3b3b3;
}
@media (max-width: 1220px) {
  .card {
    margin-top: 60%;
  }
}
.card__number {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 35px;
  position: absolute;
  top: 150px;
  left: 50px;
  color: #e2d13c;
  text-shadow: #747474 1px 1px;
}
.card__chip {
  background-image: url("../img/chip.svg");
  width: 60px;
  height: 45px;
  position: absolute;
  background-repeat: no-repeat;
  top: 80px;
  left: 20px;
}
.advantages {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}
.advantages__item {
  font-size: 20px;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 25px;
  width: 120px;
  position: absolute;
  animation: word;
  animation-fill-mode: forwards;
  border-bottom: 1px solid #fcfcfc;
  padding-bottom: 15px;
  text-align: center;
  height: 40px;
}
.advantages__item:nth-child(1) {
  top: -70px;
  left: 20px;
  animation-duration: 1s;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(1) {
    top: -55px;
    padding-bottom: 0;
  }
}
.advantages__item:nth-child(2) {
  top: -70px;
  left: 170px;
  animation-duration: 1.3s;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(2) {
    top: -55px;
    padding-bottom: 0;
  }
}
.advantages__item:nth-child(3) {
  top: 235px;
  left: 20px;
  animation-duration: 2.5s;
  padding-bottom: 0;
}
.advantages__item:nth-child(4) {
  top: 220px;
  left: 170px;
  animation-duration: 2.2s;
}
.advantages__item:nth-child(5) {
  top: 20px;
  left: -170px;
  animation-duration: 3.1s;
  text-align: left;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(5) {
    left: 20px;
    top: 305px;
    padding-bottom: 0;
    text-align: center;
  }
}
.advantages__item:nth-child(6) {
  left: -170px;
  top: 120px;
  animation-duration: 2.8s;
  text-align: left;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(6) {
    left: 170px;
    top: 290px;
    text-align: center;
  }
}
.advantages__item:nth-child(7) {
  top: 120px;
  left: 368px;
  animation-duration: 1.9s;
  text-align: right;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(7) {
    left: 20px;
    top: -120px;
    text-align: center;
  }
}
.advantages__item:nth-child(8) {
  left: 368px;
  top: 20px;
  animation-duration: 1.6s;
  text-align: right;
}
@media (max-width: 1220px) {
  .advantages__item:nth-child(8) {
    left: 170px;
    top: -105px;
    padding-bottom: 0;
    text-align: center;
  }
}
@keyframes word {
  0% {
    color: transparent;
    border: none;
  }
  10% {
    color: transparent;
  }
  100% {
    color: #f0f0f0;
  }
}
.main-cantainer {
  flex-grow: 2;
}
.page-footer {
  width: 100vw;
  padding: 40px;
  background-color: #fcfcfc81;
}
.page-footer__container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin: 0 auto;
  width: 300px;
  justify-content: center;
}
@media (min-width: 1220px) {
  .page-footer__container {
    width: 900px;
  }
}
.el {
  padding: 10px;
  font-size: 20px;
  color: #121212;
  border-radius: 7px;
  border: none;
  border-bottom: 1px solid #4c4457;
  background-color: #fcfcfc3b;
  box-sizing: border-box;
  cursor: pointer;
}
